.empty-page {
  text-align: center;
  max-width: 300px;
  color: var(--text-color);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 24px;
  margin-top: 200px;
  margin-bottom: 60px;
  font-size: 14px;
  display: none;
}

.favorites-content {
  margin-top: 44px;
  margin-bottom: 64px;
  display: flex;
}

.favorites-img-tablet, .favorites-img-desktop {
  display: none;
}

.recipe-results-fav {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 24px;
  margin-top: 50px;
  display: flex;
}

.details-card-container {
  width: 80%;
  bottom: 16px;
}

.rating {
  color: #fff;
  align-items: center;
  gap: 2px;
  display: flex;
}

.stars-container {
  display: flex;
}

.pagination-controls-fav {
  justify-content: center;
  align-items: center;
  gap: 14px;
  max-width: 600px;
  margin-top: 70px;
  margin-bottom: 100px;
  display: flex;
}

.recipe-container, .main-content {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media screen and (width >= 768px) {
  .favorites-img-mobile {
    display: none;
  }

  .empty-page {
    max-width: 400px;
    font-size: 16px;
  }

  .favorites-img-tablet {
    display: block;
  }

  .recipe-results-fav {
    flex-flow: wrap;
    justify-content: left;
    max-width: 720px;
    margin-left: 25px;
  }

  .rating {
    flex-direction: row;
  }

  .stars-container {
    width: 45px;
    display: flex;
  }

  .pagination-controls-fav {
    position: absolute;
    top: 577px;
    left: -103px;
  }

  .pagination-wrapper-fav {
    position: absolute;
  }
}

@media screen and (width >= 1280px) {
  .empty-page {
    max-width: 524px;
  }

  .favorites-img-desktop {
    display: block;
  }

  .favorites-img-tablet {
    display: none;
  }

  .recipe-results-fav {
    max-width: 1120px;
  }

  .stars-container {
    width: 75px;
    display: flex;
  }
}
